	const app = getApp()
import pickerAddress from '@/componets/wangding-pickerAddress.vue';
export default {
	components: {
		pickerAddress
	},
	data() {
		return {
			formNode:{
				area: '请选择地区',
				mobile: '',
				wx_code:''
			}
			
		};
	},
	methods: {
		inputValue(e){
			let formNode = this.formNode
			let name = e.currentTarget.dataset.name
			let value = e.detail.value
			formNode[name] = value
			this.formNode = formNode
		},
		// 省市区联动
		goAddress(e) {
			this.formNode.area = e.data.join('');
		},
		getJoin(){
			if(this.formNode.mobile == ''){
				uni.showToast({
					title: '请输入手机号',
					icon: 'none'
				})
				return false
			}
			if(this.formNode.mobile.length != 11){
				uni.showToast({
					title: '请输入正确手机号',
					icon: 'none'
				})
				return false
			}
			if(this.formNode.wx_code == ''){
				uni.showToast({
					title: '请输入微信名',
					icon: 'none'
				})
				return false
			}
			if(this.formNode.area == '请选择地区'){
				uni.showToast({
					title: '请选择地址',
					icon: 'none'
				})
				return false
			}
			uni.showLoading({
				title: '加载中...',
				icon: 'none'
			})
			uni.request({
				url: `${this.$helper.requestUrl}agent/agentApply`,
				method: 'POST',
				header: {
					authorization: app.globalData.token
				},
				data: this.formNode,
				success: res => {
					uni.hideLoading();
					res = this.$helper.null2str(res.data)
					if(res.status_code == 'ok'){
						uni.showToast({
							title: res.message,
							icon: 'none',
							duration: 2000
						})
						setTimeout(() => {
							// 进入登录页
							uni.navigateBack({
								delta: 1
							})
						}, 2000)
					}else{
						uni.showToast({
							title: res.message,
							icon: 'none'
						})
					}
				}
			})
		}
		// }
	}
};
